import Link from "next/link";
import { useSpring, animated } from "react-spring";

import { Button } from "reactstrap";

import styled from "styled-components";

const ESWButtonDiv = styled.div`
  font-family: "Charm", cursive;

  position: absolute;
  top: 15vh;
  left: 15vw;

  font-size: 2vw;

  .btn-red {
    background: rgba(192, 19, 19, 1);
    border: 1vw double rgba(7, 80, 8, 0.7);
    border-radius: 100%;
    box-shadow: 0 0.2vw 0 0.3vw #c91919 inset, 0 0.3vw 0.3vw rgba(65, 8, 8, 0.5),
      0 1.5vw rgba(255, 255, 255, 0.5) inset, 0 0 0 10px hsl(0, 0%, 80%),
      inset 0 0 10px #000000;
    cursor: pointer;
    display: inline-block;
    height: 15vw;
    width: 15vw;
  }
  .btn-red:hover {
    background: #333;
    box-shadow: 0 -2px 0 3px #1a1a1a inset, 0 5px 5px rgba(0, 0, 0, 0.17),
      0 15px rgba(255, 255, 255, 0.32) inset;
  }

  #borderDiv {
  }

  .animatedESWButtonDiv {
  }
`;

const config = { mass: 5, tension: 200, friction: 500 };

function ESWButton() {
  const ESWButtonProps = useSpring({
    config,
    opacity: 1,
    from: {
      opacity: 0
    }
  });

  return (
    <ESWButtonDiv>
      <div id="ESWButtonDiv">
        <animated.div className="animatedESWButtonDiv" style={ESWButtonProps}>
          <Link href={process.env.BACKEND_URL + "/home"}>
            <a>
              <button className="btn-red btn-red:hover">
                Enter Santa's Workshop
              </button>
            </a>
          </Link>
        </animated.div>
      </div>
    </ESWButtonDiv>
  );
}

export default ESWButton;
